

































import {
	mapState,
	mapMutations
} from 'vuex';
export default {
	computed: mapState([ 'hasLogin','userInfo']),
	data() {
		return {
			account:[],
			tabBars:[
				{name: '全部',id: 0},
				{name: '淘宝',id: 1},
				{name: '天猫',id: 2},
				{name: '京东',id: 3},
				{name: '拼多多',id: 4},
				{name: '聚划算',id: 5},
			]
		};
	},
	onLoad() {	
		if(this.hasLogin){
			uni.request({
				url:`${this.$serverUrl}/index/item`,
				header:{
					"Content-Type": "application/x-www-form-urlencoded",
					"token":this.userInfo.token
				},
				method:"GET",
				success: (res) => {//请求成功后返回的数据
					const data = res.data.info
					this.account = data.account;
				}
			})
		}else{
			uni.reLaunch({
				url:"../login/login"
			})
		}	
	},
	methods:{
		 tapTab(index){
			uni.request({
				url:`${this.$serverUrl}/index/item?scid=`+index,
				header:{
					"Content-Type": "application/x-www-form-urlencoded",
					"token":this.userInfo.token
				},
				method:"GET",
				success: (res) => {//请求成功后返回的数据
					const data = res.data.info
					this.account = data.account;
				}
			})
		},
	}
}
